<template>
	<div class="shopHeadWrap">
		<div class="bannerWrap">
			<img src="../../assets/banner.jpg" />
		</div>
		<img src="../../assets/logo.jpg" class="shopLogo" :style="{height:h}" />
		<a href="javascript:;" @click="goBlack()" class="blackBtn"><i class="iconfont icon-fanhuitianchong"></i></a>
		<a href="javascript:;" class="collectBtn" @click="collectBtn($event)"><i class="iconfont icon-shoucang1"></i></a>
	</div>
</template>

<script>
import {shopHead} from './shopHeadJs'

shopHead.components = {}

export default shopHead

</script>

<style scoped>
@import "shopHeadCss.css";
</style>



