import Velocity from 'velocity-animate'
export const shopCart = {
	data(){
		return{
			bShowCart:true
		}
	},
    watch:{
    },
	created(){
	},
	methods:{
		showCart(){
			if(this.bShowCart){
				let oDiv = this.$refs.productBackground
				let oBox = this.$refs.productList
				oDiv.style.display = "block"
				Velocity(oDiv,{
					opacity:0.6
				},800)
				Velocity(oBox,"slideDown",500)
				this.bShowCart = false
			}
		},
		hideCart(){
			let oDiv = this.$refs.productBackground
			let oBox = this.$refs.productList
			Velocity(oDiv,{
				opacity:0
			},{
				complete:elements => {
					oDiv.style.display = 'none'
				}
			},800)
			Velocity(oBox,"slideUp",500)
			this.bShowCart = true
		} 
    },
    components:{}
}



