<template>
	<div class="registeredWrap">
		<Header>
			<span slot="title">会员注册</span>
		</Header>
		<div class="reg-con-wrap">
			<div class="reg-con-main">
				<ul>
					<li>
						<span>用户名：</span>
						<input type="text" name="userName" placeholder="请输入用户名" ref="userName" @keyup="checkName($event)" />
					</li>
					<li>
						<span>密码：</span>
						<input type="password" name="userPassword" placeholder="请输入密码" ref="userPassword" @keyup="checkPassword($event)" />
					</li>
					<li>
						<span>确认密码：</span>
						<input type="password" name="userPassword2" placeholder="请再次输入密码" ref="userPassword2" @keyup="checkPassword($event)" />
					</li>
				</ul>
			</div>
			<a href="javascript:;" class="reg-submit" @click="regBtn()">注册</a> 
		</div>
	</div>
</template>

<script>
import {registered} from './registeredJs'
import Header from '@/components/header/header'

registered.components = {
	Header
}

export default registered

</script>

<style scoped>
@import "registeredCss.css";
</style>



