<template>
  <div class="shopWrap">
  	<shop-head></shop-head>
  	<shop-activity></shop-activity>
  	<shop-product></shop-product>
  	<shop-cart></shop-cart>
  </div>
</template>

<script>
import {shop} from './shopJs'
import shopHead from '@/components/shopHead/shopHead'
import shopActivity from '@/components/shopActivity/shopActivity'
import shopProduct from '@/components/shopProduct/shopProduct'
import shopCart from '@/components/shopCart/shopCart'


shop.components = {
	shopHead,
	shopActivity,
	shopProduct,
	shopCart
}

export default shop

</script>

<style scoped>
@import "shopCss.css";
</style>



