export const login = {
	data(){
		return {}
	},
	methods:{
		loginBtn(){
			let name = this.$refs.userName.value
			let password = this.$refs.userPassword.value
			if(name == '123' && password == '123'){
				this.$router.push('/member')
			}else{
				alert("用户名或密码错误")
			}
		}
    },
    components:{}
}




