import {getStyle} from '@/assets/base'
export const shopProduct = {
	data(){
		return{
			curr:0
		}
	},
    watch:{
    },
	created(){
	},
	mounted(){
		this.setHeight()
	},
	methods:{ 
		setHeight(){
			let oRight = this.$refs.shopProductRight
			let aLi = oRight.getElementsByTagName('li') 
			let productImg = oRight.getElementsByClassName("productImg")
			let w = parseFloat(getStyle(aLi[0],'width'))
			for(let i=0;i<productImg.length;i++){
				productImg[i].style.height =`${w*0.26}px`
			}
		},
		tabProduct(n){
			this.curr = n
		}
    },
    components:{}
}



