<template>
	<div class="shopCartWrap">
		<div class="productBackground" ref="productBackground" @click="hideCart()"></div>
		<div class="productList" ref="productList">
			<div class="productListHead">
				<h3>购物车列表</h3>
				<a href="javascript:;" class="clearCart"><i class="iconfont icon-qingkongshanchu"></i>清空购物车</a>
			</div>
			<div class="productListMain">
				<ul>
					<li>
						<span class="productName">商品1</span>
						<div class="countWrap">
							<a href="javascript:;" class="subtract"><i class="iconfont icon-jianhao"></i></a>
							<span>0</span>
							<a href="javascript:;" class="add"><i class="iconfont icon-jiahao-copy"></i></a>
						</div>
						<span class="productPrice">￥12.00</span>
					</li>
				</ul>
			</div>
		</div>
		<div class="shopCartMain">
			<div class="shopCart">
				<a href="javascript:;" class="shopCartBtn" @click="showCart()">
					<i class="iconfont icon-ziyuan"></i>
					<span>9</span>
				</a>
			</div>
			<div class="totalMain">
				<span class="total">￥12.00</span>
				<p>另需配送费0.00</p>
			</div>
			<router-link to="/addOrder" class="aBuy">去结算</router-link>
		</div>
	</div>
</template>

<script>
import {shopCart} from './shopCartJs'

shopCart.components = {}

export default shopCart

</script>

<style scoped>
@import "shopCartCss.css";
</style>



