import {getStyle} from '@/assets/base'
export const shopActivity = {
	data(){
		return{

		}
	},
    watch:{
    },
	created(){
	},
	mounted(){
		this.getUlWidth()
		this.ulMove()
	},
	methods:{ 
		getUlWidth(){
			let oUl = this.$refs.bulletinUl
			let aLi = oUl.children
			let w = 0
			for(let i=0;i<aLi.length;i++){
				w += parseFloat(getStyle(aLi[i],'width'))
			}
			oUl.style.width = `${w}px`
		},
		ulMove(){
			let oMain = this.$refs.bulletinMain
			let oUl = this.$refs.bulletinUl
			let w = parseFloat(getStyle(oMain,'width'))
			let w2 = parseFloat(getStyle(oUl,'width'))
			let iTime = null
			let num = 0
			iTime = setInterval(()=>{
				num -=4
				if(num<= -w2){
					num = w
				}
				oUl.style.left = `${num}px`	
			},200)
		}
    },
    components:{}
}



