<template>
    <div class="loginWrap">
    	<Header>
			<span slot="title">会员登录</span>
			<router-link to="/registered" slot="right">注册</router-link>
		</Header>
		<div class="loginMain">
			<img src="../../assets/logo.jpg" class="logo" />
			<ul>
				<li>
					<span><i class="iconfont icon-ren"></i></span>
					<input type="text" name="userName" placeholder="请输入用户名" ref="userName" />
				</li>
				<li>
					<span><i class="iconfont icon-mima"></i></span>
					<input type="password" name="userPassword" placeholder="请输入密码" ref="userPassword" />
				</li>
			</ul>
			<a href="javascript:;" class="loginBtn" @click="loginBtn()">登录</a>
		</div>
    </div>
</template>

<script>
import {login} from './loginJs'
import Header from '@/components/header/header'

login.components = {
	Header
}

export default login

</script>

<style scoped>
@import "loginCss.css";
</style>



