export const shopHead = {
	data(){
		return{
			h:`${window.innerWidth*0.2}px`
		}
	},
    watch:{
    },
	created(){
	},
	mounted(){
	    window.onresize = () =>{
	        this.h = `${window.innerWidth*0.2}px`
	    }
	},
	methods:{ 
		goBlack(){this.$router.go(-1)},
		collectBtn(e){
			if(e.target.tagName.toLowerCase() == 'i'){
				if(e.target.className == 'iconfont icon-shoucang'){
					e.target.className = 'iconfont icon-shoucang1'
				}else if(e.target.className == 'iconfont icon-shoucang1'){
					e.target.className = 'iconfont icon-shoucang'
				}
			}
		}
    },
    components:{}
}



