export const registered = {
	data(){
		return{
		}
	},
    watch:{
    },
	created(){
	},
	methods:{ 
		checkName(e){
			e.target.value=e.target.value.replace(/[^\w\.\/]/ig,'')
		},
		checkPassword(e){
			e.target.value=e.target.value.replace(/[\u4E00-\u9FA5]|[\uFE30-\uFFA0]/g,'')
		},
		regBtn(){
			let name = this.$refs.userName.value
			let password = this.$refs.userPassword.value
			let password2 = this.$refs.userPassword2.value
			if(password == password2){
				this.$router.push('/login')
			}else{
				alert("两次输入的密码不一致")
			}
		}
    },
    components:{}
}



